@import "../../global.scss";

.intro {
  background-color: white;
  display: flex;

  @include mobile{
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imgContainer {
      height: 700px;
      width: 700px;
      background-color: #add8e6;
      border-radius: 50%;
      display: flex;
      align-items: flex-end;
      justify-content: right;
      float: right;

      @include mobile{
        justify-content: center;
        align-items: flex-start;
      }

      img {
        height: 90%;

        @include mobile{
          height: 50%;
        }
      }
    }
  }
  .right {
    flex: 0.5;
    position: relative;

    .wrapper {
      width: 100%;
      height: 100%;
      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile{
        padding-left: 0;
        align-items: center;
      }
    }

    h1 {
      font-size: 55px;
      margin: 10px 0;

      @include mobile{
        font-size: 38px;
      }
    }

    h2 {
      font-size: 35px;
    }

    h3 {
      font-size: 30px;

      @include mobile{
        font-size: 25px;
      }

      span {
        color: crimson;
      }

      .ityped-cursor {
        animation: blink 1s infinite;
      }
      @keyframes blink {
        50% {
          opacity: 1;
        }
        100% {
          opacity: 0;
        }
      }
    }

    a {
      position: absolute;
      bottom: 10px;
      left: 50%;

      img {
        width: 30px;
        animation: arrowBlink 2s infinite;
      }

      @keyframes arrowBlink {
        100% {
          opacity: 0%;
        }
      }
    }
  }
}
